import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SetupService, SetUp, AdmissionSettings, Classes } from './setup.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
@Component({
  selector: 'app-setup',
  templateUrl: './setup.component.html',
  styleUrls: ['./setup.component.sass']
})
export class SetupComponent implements OnInit {

  // domain: any;
  // loginForm: FormGroup;
  checked = false;
  indeterminate = false;
  labelPosition: 'before' | 'after' = 'after';
  disabled = false;
  settings: AdmissionSettings = new AdmissionSettings();
  classes: Classes[];
  aclass: Classes = new Classes();
  submitted = false;
  returnUrl: string;
  hide = true;
  success: any;
  error: any;
  isDarkSidebar = false;
  seasons: string[] = ['Winter', 'Spring', 'Summer', 'Autumn'];
  favoriteSeason: string;
  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private setupService: SetupService,
    private snackBar: MatSnackBar
  ) {
    // route.params.subscribe(params => { this.domain = params['id']; });
  }
  ngOnInit() {
    this.getSettings();
    this.getClasses();
    // this.settings.exam_directly = true;
    
  }
 
  showNotification(colorName, text, placementFrom, placementAlign) {
    this.snackBar.open(text, '', {
      duration: 2000,
      verticalPosition: placementFrom,
      horizontalPosition: placementAlign,
      panelClass: colorName,
    });
  }

  editSettings(): void {
    this.setupService.editSettings(
      this.settings.id,
      this.settings.auto_approve_prospective_student,
      this.settings.skip_exam,
      this.settings.exam_directly,
      this.settings.auto_release_exam_score,
      this.settings.failed_stud_rewrite_exam,
      this.settings.auto_enroll_with_percentage,
      this.settings.percentage_fee_to_auto_enroll,
      this.settings.validate_all_documents_before_enrolling
    ).subscribe((res) => {
      // console.log(this.login.role);
      console.log(res);
      // console.log(res.data.id);
      console.log("edit settings working");
      this.showNotification(
        'snackbar-success',
        'Set Up successfully updated...!!!',
        'bottom',
        'center'
      );
      this.getSettings();
      this.success = res;
      // this.success = "Your super admin account has been successfully created"
      
    }, (error) => {
      console.error(error);
      console.log("edit settings not working");
      
    });

  }

  editClass(id, name, available ): void {
    // this.aclass.available = !this.aclass.available;
    available = !available
    this.setupService.editClass(
      id,
      name,
      available
    ).subscribe((res) => {
      // console.log(this.login.role);
      console.log(res);
      // console.log(res.data.id);
      console.log("edit class working");
      this.showNotification(
        'snackbar-success',
        'Set Up successfully updated...!!!',
        'bottom',
        'center'
      );
      this.getClasses();
      this.success = res;
      // this.success = "Your super admin account has been successfully created"
      
    }, (error) => {
      console.error(error);
      console.log("edit class not working");
      
    });

  }

  lightSidebarBtnClick() {

    console.log("light worked");

  }

  darkSidebarBtnClick() {
    console.log("light worked");
  }


  getSettings() {
    
    this.setupService.getSettings()
     .subscribe(
       (response) => {
         this.settings = response[0];
         console.log(response);
        //  this.getParentProfile();
        console.log("get settings working");

       }, (error) => {
         console.error(error);
         console.log("get settings not working");
       }
     );

  }

  getClasses() {
    
    this.setupService.getClasses()
     .subscribe(
       (response) => {
         this.classes = response;
         console.log(response);
         console.log("get classes working");
        //  this.getParentProfile();

       }, (error) => {
         console.error(error);
         console.log("get classes not working")

       }
     );

  }


}
