export class Classes{
  constructor(
      public id?: any,
      public name?: string,
      public available?: any
    ) {}
}

export class AdmissionSettings {
  constructor(
      public id?: any,
      public auto_approve_prospective_student?: any,
      public skip_exam?: any,
      public exam_directly?: any,
      public auto_release_exam_score?: any,
      public failed_stud_rewrite_exam?: any,
      public auto_enroll_with_percentage?: any,
      public percentage_fee_to_auto_enroll?: string,
      public validate_all_documents_before_enrolling?: any,
    ) {}
}

